
import { defineComponent, onMounted } from 'vue'
import { ref } from 'vue'
import { echarts } from '@/lib/echarts'
import { fakeData as popularList } from '@/fakeData/fakeData_dashboard'

export default defineComponent({
  setup() {
    // calendar
    let calendar = ref(new Date())

    // progress
    interface ProgressListInterface {
      page: string
      pageName: string
      date: string
      percent: number
    }
    const progressList: Array<ProgressListInterface> = [
      {
        page: 'Dashboard',
        pageName: '儀表版',
        date: '2022-09-27',
        percent: 90
      },
      {
        page: 'Order',
        pageName: '訂單',
        date: '2022-XX-XX',
        percent: 10
      },
      {
        page: 'User',
        pageName: '使用者',
        date: '2022-XX-XX',
        percent: 12
      }
    ]

    // chart
    let loading = ref(true)
    const svg = `
        <path class="path" d="
          M 30 15
          L 28 17
          M 25.61 25.61
          A 15 15, 0, 0, 1, 15 30
          A 15 15, 0, 1, 1, 27.99 7.5
          L 15 15
        " style="stroke-width: 4px; fill: rgba(0, 0, 0, 0)"/>
      `

    setTimeout(() => {
      loading.value = false
    }, 1000);

    const chartRef = ref(null)
    const option = {
      xAxis: {
        data: popularList.map(item => {
          return item.language
        })
      },
      yAxis: {
        type: 'value',
        name: '使用與熱門占比',
        min: 0,
        max: 80,
        position: 'left',
        axisLabel: {
          formatter: '{value} %'
        }
      },
      series: [
        {
          type: 'bar',
          data: popularList.map(item => {
            return item.percent
          })
        }
      ]
    }

    onMounted(() => {
      if (chartRef.value !== null) {
        const myChart = echarts.init((chartRef.value as unknown as HTMLElement))
        myChart.setOption(option)
      }
    })

    return {
      calendar,
      progressList,
      loading,
      svg,
      chartRef
    }
  }

})
